.b-clearfix {
	&:before, &:after {
		content: " ";
		display: table; }
	&:after {
		clear: both; } }
@mixin b-clear($clear: both) {
	clear: $clear; }

@mixin b-relative($relative: relative) {
	position: $relative; }

@mixin b-absolute($abs: absolute) {
	position: $abs; }

@mixin b-static($static: static) {
	position: $static; }

@mixin b-fixed($fixed: fixed) {
	position: $fixed; }

@mixin b-pointer($cur: pointer) {
	cursor: $cur; }

@mixin b-block($block: block) {
	display: $block; }

@mixin b-ib($ib: inline-block) {
	display: $ib; }

@mixin b-underline($underline: underline) {
	text-decoration: $underline; }

@mixin b-nounderline($nound: none) {
	text-decoration: $nound; }

@mixin b-displaynoneY($dny: none) {
	display: $dny; }




@mixin b-flex($df: -webkit-box, $df2: -moz-box, $df3: -ms-flexbox, $df4: -webkit-flex, $df5: flex) {
	display: $df;
	display: $df2;
	display: $df3;
	display: $df4;
	display: $df5; }

@mixin b-flex-align($align) {
	-ms-flex-align: $align;
	-webkit-align-items: $align;
	-webkit-box-align: $align;
	align-items: $align; }

@mixin b-flex-align-end($align1: end, $align2: flex-end) {
	-ms-flex-align: $align2;
	-webkit-align-items: $align2;
	-webkit-box-align: $align1;
	align-items: $align2; }

@mixin b-flex-direction($dir) {
	-ms-flex-direction: $dir;
	flex-direction: $dir;
	-webkit-box-orient: $dir;
	-webkit-flex-direction: $dir; }

@mixin b-flex-column($dir: column, $dir2: vertical) {
	-ms-flex-direction: $dir;
	flex-direction: $dir;
	-webkit-box-orient: $dir2; }

@mixin b-rounded($radius) {
	-webkit-border-radius: $radius;
	-moz-border-radius: $radius;
	-ms-border-radius: $radius;
	border-radius: $radius; }

@mixin b-opacity($opacity) {
	opacity: $opacity; }

@mixin b-transition-duration($duration) {
	-moz-transition-duration: $duration;
	-webkit-transition-duration: $duration;
	-o-transition-duration: $duration;
	transition-duration: $duration; }

@mixin b-transition($transition) {
	-webkit-transition: $transition;
	-moz-transition: $transition;
	-ms-transition: $transition;
	-o-transition: $transition;
	transition: $transition; }

@mixin b-transform($arguments) {
	-webkit-transform: $arguments;
	-moz-transform: $arguments;
	-o-transform: $arguments;
	-ms-transform: $arguments;
	transform: $arguments; }

@mixin b-rotation($deg) {
	@include b-transform(rotate($deg)); }

@mixin b-box-shadow($arguments) {
	-webkit-box-shadow: $arguments;
	-moz-box-shadow: $arguments;
	-o-box-shadow: $arguments;
	box-shadow: $arguments; }

@mixin b-fs($fontsize, $lineheight) {
	font-size: $fontsize;
	line-height: $lineheight; }

@mixin b-ls($ls) {
	letter-spacing: ($ls/1000em); }

@mixin b-uppercase($trans: uppercase) {
	text-transform: $trans; }
@mixin b-justify($just: justify, $just2: space-between) {
	-webkit-box-pack: $just;
	-moz-box-pack: $just;
	-ms-flex-pack: $just2;
	-webkit-justify-content: $just2;
	justify-content: $just2; }
@mixin b-unjustify($just: inherit) {
	-webkit-box-pack: $just;
	-moz-box-pack: $just;
	-ms-flex-pack: $just;
	-webkit-justify-content: $just;
	justify-content: $just; }

@mixin b-justify-around($just: space-around) {
	-webkit-box-pack: $just;
	-moz-box-pack: $just;
	-ms-flex-pack: $just;
	-webkit-justify-content: $just;
	justify-content: $just; }
@mixin b-justify-end($just: flex-end, $just2: end) {
	-webkit-box-pack: $just2;
	-moz-box-pack: $just;
	-ms-flex-pack: $just;
	-webkit-justify-content: $just;
	justify-content: $just; }
@mixin b-justify-center($just: center) {
	-webkit-box-pack: $just;
	-moz-box-pack: $just;
	-ms-flex-pack: $just;
	-webkit-justify-content: $just;
	justify-content: $just; }
@mixin b-flex-wrap($event: wrap) {
	flex-wrap: $event;
	-webkit-flex-wrap: $event; }
@mixin b-wh($width: auto, $height: auto) {
	width: $width;
	height: $height; }
@mixin b-bg-gradient($default, $args) {
	background: $default;
	background: -webkit-gradient($args);
	background: -webkit-linear-gradient($args);
	background: -moz-linear-gradient($args);
	background: -o-linear-gradient($args);
	background: linear-gradient($args); }


.b-justify {
	-webkit-box-pack: justify;
	-moz-box-pack: justify;
	-ms-flex-pack: space-between;
	-webkit-justify-content: space-between;
	justify-content: space-between; }
.b-flex {
	@include b-flex; }

